
// 

import { useParams } from "react-router-dom"
import axios from "axios"
import { useEffect, useState, useMemo } from "react"
import { useAxios, forceLogin } from "../components/hooks"
import _ from 'lodash'
import './ViewVote.css'
import dayjs from "dayjs"
import { useImmer } from "use-immer"


/**
 * 
 * 1. 
 * 2. comet
 * hold
 * 
 * 3.websocket
 *
 */

function ViewVote({ userInfo: userData }) { //userInfo
    const userId = userData.result.userId //ID
    const { voteId } = useParams()
    const [userVotes, setUserVotes] = useState() //ws
    // const { data: userData, loading: userLoading, error: userError } = useUser() //
    const { data, loading, error } = useAxios({ //useAxiosws
        url: '/vote/' + voteId,
        method: 'GET'
    }, voteId)

    const [selectedOptionIds, setSelectedOptionIds] = useImmer([]) //

    //ws
    useEffect(() => {
        // console.log('useEffect ws');
        //
        if (loading || Date.now() > new Date(data.result.deadline).valueOf()) { //
            return
        }
        // ws://localhost:8081/realtime-voteinfo/voteId
        const wsURL = `${window.location.protocol.replace('http', 'ws')}//${window.location.host}/realtime-voteinfo/${voteId}`
        console.log(wsURL);
        const ws = new WebSocket(wsURL)
        ws.addEventListener('message', e => {
            let data = JSON.parse(e.data) //  userVotes <json>
            console.log('ws', data);
            setUserVotes(v => data)
        })
        return () => {
            console.log('ws');
            ws.close()
        }
    }, [voteId, loading])



    let vote = data?.result ?? {} //  {voteId, userId, deadline,.., options[], userVotes[]}
    // console.log('vote', vote);
    let thisPageUserVotes = (userVotes ?? vote.userVotes) || [] //vote.userVotes [{optionId, avatar, userId, name}]
    // console.log('thisPageUserVotes', thisPageUserVotes);
    let groupedVotes = useMemo(() => _.groupBy(thisPageUserVotes, 'optionId'), [thisPageUserVotes])//optionId
    let uniqueUsers = useMemo(() => _.uniqBy(thisPageUserVotes, 'userId'), [thisPageUserVotes]) //
    let totalUsers = uniqueUsers.length; //
    console.log('groupedVotes', groupedVotes);
    console.log('totalUsers:', totalUsers);


    //   /
    async function voteOption(option) {
        //
        if (!vote.anonymous) {
            const { optionId } = option
            axios.post(`/vote/${voteId}`, {
                optionIds: [optionId]
            }) //
        } else {
            axios.post(`/vote/${voteId}`, {
                optionIds: selectedOptionIds
            }) //
            setSelectedOptionIds(draft => [])
        }
    }
    // ||   || 
    function handleOptionClick(option) {
        if (vote.anonymous) {
            //
            if (thisPageUserVotes.some(opration => opration.userId === userId)) {
                return
            }
            selectOption(option)
        } else {
            voteOption(option)
        }
    }

    function selectOption(option) {
        const optionId = option.optionId
        let idx = selectedOptionIds.indexOf(optionId)
        if (idx === -1) {
            setSelectedOptionIds(draft => {
                draft.push(optionId)
            })
        } else {
            setSelectedOptionIds(draft => {
                draft.splice(idx, 1)
            })
        }
    }

    //
    if (loading) return 'Loading...'
    if (error instanceof Error) {
        console.log('viewvoteaxios', error);
        throw error
    }
    // if (userError) { //
    //     return <RequireLogin />
    // }
    return (
        <div>
            <h1></h1>
            <h2 title={vote.description}>title: {vote.title}</h2>
            <p style={{ color: '#3269da' }}>
                [{vote.multiple ? '' : ''}]
                [{vote.anonymous ? '' : ''}]
            </p>
            {/* <h3>description: {vote.description}</h3> */}
            <ul>
                {vote.options.map((option, idx) => {
                    // console.log(option); // {optionId, content, voteId}
                    let thisOptionVoted = groupedVotes[option.optionId] || [] //optionId {optionId, avatar, userId, name}
                    // console.log('thisOptionVoted', thisOptionVoted);
                    return (
                        <li onClick={() => handleOptionClick(option)} key={idx} style={{ border: '1px solid red' }}>
                            [{option.content}]
                            {`[${thisOptionVoted.length}]`}
                            [{totalUsers == 0 ? 0 : (thisOptionVoted.length * 100 / totalUsers).toFixed(2)}%]
                            {/*  */}
                            {(thisOptionVoted.some(user => user.userId === userId) ||
                                selectedOptionIds.includes(option.optionId))
                                ? '[]'
                                : null
                            }
                            <div>
                                {/*  */}
                                {
                                    thisOptionVoted.map((oneVote, idx) => (
                                        <img key={idx} className='user-avatar'
                                            src={oneVote.avatar ? oneVote.avatar : `http://localhost:8081/uploads/default.jpg`}
                                            title={oneVote.name || ''}
                                        ></img>
                                    ))
                                }
                            </div>

                        </li>
                    )
                })}
            </ul>
            <div>
                {/*  */}
                {!!vote.anonymous && !thisPageUserVotes.some(opration => opration.userId == userId) &&
                    <button disabled={!selectedOptionIds.length} onClick={voteOption}></button>
                }
            </div>
            <p>{dayjs(vote.deadline).format('YYYY-MM-DD HH:mm')}</p>
        </div>
    )
}

export default forceLogin(ViewVote)