
// import axios from "../api" //API1
import axios from "axios"
import { useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { useInput, useUser } from '../components/hooks'





// 
export default function Login(props) {
    const userName = useInput()
    const password = useInput()
    const navigate = useNavigate()
    const userInfo = useUser() // /account/current-user -> useAxios {data, loading, error, update}

    //
    useEffect(() => {
        if (userInfo.data) {
            navigate(-1)
        }
    })

    //  axios
    async function userLogin() {
        const info = {
            name: userName.value,
            password: password.value
        }
        //axios,data
        // let response = await axios.post('/account/login', info, {
        //     withCredentials: true //Cookie fetch"credentials:'include'"
        // }) //API
        let response, data
        try {
            response = await axios.post('/account/login', info) //APIcookie
            // let data = response.data //
            console.log('', response);
            //cookie
            userInfo.update() //cookie<RequireLogin>
            navigate(-1)
        } catch (e) { //1. >=400 axios 2.
            // console.log(e.toJSON());
            // console.log(e.response.data); //
            if (e.response) {
                alert(e.response.data.msg) //  {code : -1, msg: 'xxx'} 
            } else {
                alert('you have net error, then axios failed request.')
            }
        }




    }

    return (
        <div>
            <div></div>
            <input type="text" {...userName} />
            <div></div>
            <input type="password" {...password} />
            <div><button onClick={() => userLogin()}></button></div>
        </div>
    )
}