import { forceLogin, useAxios } from "../components/hooks"

import { Link } from "react-router-dom"

// 
function MyVotes({ userInfo }) {
    // console.log('MyVotesuserInfo', userInfo); // {code:0, result:.. }
    const loginUser = userInfo.result // {userId name avatar}
    //
    const { data, error, loading } = useAxios({ url: '/vote' }) //cookie
    if (loading) return 'Loading..'
    //  if(error) //
    console.log('', data);

    // 
    return (
        <div>
            
            <ul>
                {
                    data.result.map(vote => {
                        return (
                            <li key={vote.voteId}>
                                <Link to={`/view-vote/${vote.voteId}`}>{vote.title}</Link>
                                <span style={{ color: '#3269da' }}>[{vote.multiple ? '' : ''}]</span>
                                <span style={{ color: '#3269da' }}>[{vote.anonymous ? '' : ''}]</span>
                            </li>
                        )
                    })
                }
            </ul>
        </div>
    )
}

export default forceLogin(MyVotes)