
import { Link } from "react-router-dom"
import './CreateUI.css'


//
export default function CreateUI(props) {
    return (
        <div>
            <div className="card">
                <img className="card-img" src="" alt="" />
                <Link to='/create-vote'></Link>
            </div>
            <div className="card">
                <img className="card-img" src="" alt="" />
                <Link to='/create-vote?multiple=1'></Link>
            </div>
        </div>
    )
}